import './scss/style.scss';

$(document).ready(function () {
    $('#banner-carousel').owlCarousel({
        items: 1,
        autoplay: true,
        autoplayTimeout: 5000,
        animateOut: 'fadeOut',
        animateIn: 'fadeIn',
        loop: true,
        dots: false,
        nav: false,
        mouseDrag: false,
        touchDrag: false,
        margin: 0,
    })
    jQuery("#testimonials-carousel").owlCarousel({
        center: false,
        items:2,
        loop:true,
        dots: true,
        margin:30,
        responsive:{
            1000:{
                items:2
            },
            600:{
                items:1
            },
            0:{
                items:1
            }
        }
     });
    // --------------------------------------------------
    // tabs
    // --------------------------------------------------
    jQuery('.steps__content').find('.steps__tab > div').hide();
    jQuery('.steps__content').find('.steps__tab > div:first').show();
    jQuery('li').find('.v-border').fadeTo(150, 0);
    jQuery('li.active').find('.v-border').fadeTo(150, 1);

    jQuery('.steps__nav li').click(function () {
        jQuery(this).parent().find('li').removeClass("active");
        jQuery(this).addClass("active");
        jQuery(this).parent().parent().find('.v-border').fadeTo(150, 0);
        jQuery(this).parent().parent().find('.steps__tab > div').hide();

        var indexer = jQuery(this).index(); //gets the current index of (this) which is #nav li
        jQuery(this).parent().parent().find('.steps__tab > div:eq(' + indexer + ')').fadeIn(); //uses whatever index the link has to open the corresponding box 
        jQuery(this).find('.v-border').fadeTo(150, 1);
    });

    $(window).on('scroll',function(){
        if($(window).scrollTop()){
            $('#main-nav').addClass('archi-sticky');
        }
        else{
            $('#main-nav').removeClass('archi-sticky');
        }
        
        if($(window).scrollTop()){
            $('#main-body').addClass('ml25');
        }
        else{
            $('#main-body').removeClass('ml25');
        }
      
    })
    $(window).on('scroll', function () {
        if ($(this).scrollTop() > 600) {
          $('.scroll-top').fadeIn(600);
        } else {
          $('.scroll-top').fadeOut(600);
        }
      });
    $('.scroll-top').on("click", function () {
        $("html,body").animate({
            scrollTop: 0
        }, 500);
        return false;
    });

    $('.nav-link').smoothScroll();
});